import { Button } from 'ether-ui';
import env from '../../common/env';

function RegistrationSuccess({ user }: any) {
    return (
        <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
            <div className="max-w-md w-full bg-white rounded-2xl shadow-xl p-8 text-center">
                <div className="w-20 h-20 bg-green-100 rounded-full mx-auto mb-6 flex items-center justify-center">
                    <span className="text-green-600 text-3xl">âœ“</span>
                </div>

                <h2 className="text-2xl font-bold text-gray-900 mb-4">Registration Successful!</h2>

                <div className="space-y-4 text-left">
                    {env.EMAIL_VERIFICATION_REQUIRED && !user?.isEmailVerified && (
                        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                            <h3 className="font-semibold text-blue-800 mb-2">ðŸ“§ Email Verification</h3>
                            <p className="text-blue-700 text-sm">
                                Please check your email and click the verification link to activate your account.
                            </p>
                        </div>
                    )}

                    {env.PHONE_VERIFICATION_REQUIRED && !user?.isPhoneVerified && (
                        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
                            <h3 className="font-semibold text-green-800 mb-2">ðŸ“± Phone Verification</h3>
                            <p className="text-green-700 text-sm">
                                We've sent a verification code to your phone. Please enter it when you log in.
                            </p>
                        </div>
                    )}

                    {!env.EMAIL_VERIFICATION_REQUIRED && !env.PHONE_VERIFICATION_REQUIRED && (
                        <div className="bg-green-50 border border-green-200 rounded-lg p-4">
                            <p className="text-green-700">Your account is ready to use! You can now sign in with your credentials.</p>
                        </div>
                    )}
                </div>

                <Button
                    href="/login"
                    className="w-full mt-6 bg-linear-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white py-3 rounded-lg font-semibold transition-all duration-200"
                >
                    Continue to Sign In
                </Button>
            </div>
        </div>
    );
}

export default RegistrationSuccess;
