import { useState } from 'react';
import { RegisterRequest } from '../../../types/auth';
import { Button, Password, TextInput } from '../../components';
import { useAuthStore } from '../../services/auth';
import NewUserMarketingBanner from './NewUserMarketingBanner';
import RegistrationSuccess from './RegistrationSuccess';

const Register = () => {
    const register = useAuthStore((s) => s.register);
    const [formData, setFormData] = useState<RegisterRequest>({
        name: '',
        email: '',
        phone: '',
        organizationName: '',
        organizationWebsite: '',
        loginId: '',
        password: '',
    });
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState(false);
    const [registeredUser, setRegisteredUser] = useState<any>(null);

    const handleInputChange = ({ value, name }: { value: string; name?: string }) => {
        setFormData((prev) => ({ ...prev, [name!]: value }));
        if (error) setError('');
    };

    const validateForm = () => {
        const required = ['name', 'email', 'phone', 'organizationName', 'loginId', 'password'];
        const missing = required.filter((field) => !formData[field as keyof RegisterRequest]);

        if (missing.length > 0) {
            setError(`Please fill in: ${missing.join(', ')}`);
            return false;
        }

        // Email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(formData.email)) {
            setError('Please enter a valid email address');
            return false;
        }

        // Phone validation
        const phoneRegex = /^\+?[\d\s-()]+$/;
        if (!phoneRegex.test(formData.phone)) {
            setError('Please enter a valid phone number');
            return false;
        }

        // Password validation
        if (formData.password.length < 8) {
            setError('Password must be at least 8 characters long');
            return false;
        }

        return true;
    };

    const handleSubmit = async () => {
        if (!validateForm()) return;

        setLoading(true);
        setError('');

        try {
            const response = await register(formData);
            setSuccess(true);
            setRegisteredUser(response.user);
        } catch (error: any) {
            setError(error?.message || 'Registration failed. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    if (success) {
        return <RegistrationSuccess user={registeredUser} />;
    }

    return (
        <div className="min-h-screen flex">
            <NewUserMarketingBanner />

            {/* Registration Form Section */}
            <div className="w-full lg:w-3/5 flex items-center justify-center p-6 bg-gray-50">
                <div className="w-full max-w-lg">
                    <div className="bg-white rounded-2xl shadow-xl p-8">
                        <div className="text-center mb-8">
                            <div className="w-16 h-16 bg-linear-to-r from-purple-600 to-blue-600 rounded-xl mx-auto mb-4 flex items-center justify-center">
                                <span className="text-white text-2xl font-bold">OB</span>
                            </div>
                            <h2 className="text-3xl font-bold text-gray-900 mb-2">Create Account</h2>
                            <p className="text-gray-600">Join OmniBlast and start your marketing automation</p>
                        </div>

                        <div className="space-y-5">
                            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                <div>
                                    <TextInput
                                        label="Full Name"
                                        required
                                        name="name"
                                        placeholder="John Doe"
                                        value={formData.name}
                                        onChange={handleInputChange}
                                        disabled={loading}
                                        className="mt-1"
                                    />
                                </div>

                                <div>
                                    <TextInput
                                        label="Email Address"
                                        required
                                        name="email"
                                        type="email"
                                        placeholder="john@company.com"
                                        value={formData.email}
                                        onChange={handleInputChange}
                                        disabled={loading}
                                        className="mt-1"
                                    />
                                </div>
                            </div>

                            <div>
                                <TextInput
                                    label="Phone Number"
                                    required
                                    name="phone"
                                    placeholder="+1 (555) 123-4567"
                                    value={formData.phone}
                                    onChange={handleInputChange}
                                    disabled={loading}
                                    className="mt-1"
                                />
                            </div>

                            <div>
                                <TextInput
                                    label="Organization Name"
                                    required
                                    name="organizationName"
                                    placeholder="Your Company Name"
                                    value={formData.organizationName}
                                    onChange={handleInputChange}
                                    disabled={loading}
                                    className="mt-1"
                                />
                            </div>

                            <div>
                                <TextInput
                                    label="Organization Website"
                                    required
                                    name="organizationWebsite"
                                    placeholder="https://yourcompany.com"
                                    value={formData.organizationWebsite}
                                    onChange={handleInputChange}
                                    disabled={loading}
                                    className="mt-1"
                                />
                            </div>

                            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                <div>
                                    <TextInput
                                        label="Login ID"
                                        required
                                        name="loginId"
                                        placeholder="johndoe123"
                                        value={formData.loginId}
                                        onChange={handleInputChange}
                                        disabled={loading}
                                        className="mt-1"
                                    />
                                </div>

                                <div>
                                    <Password
                                        label="Password"
                                        required
                                        name="password"
                                        placeholder="Min 8 characters"
                                        value={formData.password}
                                        onChange={handleInputChange}
                                        disabled={loading}
                                        className="mt-1"
                                    />
                                </div>
                            </div>

                            {error && (
                                <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                                    <p className="text-red-600 text-sm">{error}</p>
                                </div>
                            )}

                            <Button
                                onClick={handleSubmit}
                                className="w-full bg-linear-to-r from-purple-600 to-blue-600 hover:from-purple-700 hover:to-blue-700 text-white py-3 rounded-lg font-semibold transition-all duration-200 transform hover:scale-105"
                                disabled={loading}
                            >
                                {loading ? 'Creating Account...' : 'Create Account'}
                            </Button>
                        </div>

                        <div className="mt-6 text-center">
                            <p className="text-gray-600">
                                Already have an account?{' '}
                                <a
                                    href="/login"
                                    className="text-purple-600 hover:text-purple-700 font-semibold hover:underline transition-colors"
                                >
                                    Sign in here
                                </a>
                            </p>
                        </div>
                    </div>

                    <div className="mt-6 text-center text-xs text-gray-500">
                        <p>By creating an account, you agree to our Terms of Service and Privacy Policy</p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Register;
